<template>
  <div class="page">
    <div flex class="mb-20 user-info">
      <div flex-box="0" style='margin-right:1rem;'>
        <img class="user-avatar" style='width:2.5rem;height:2.5rem;'>
      </div>
      <div flex-box="1">
        <div class="mb-10"></div>
        <div class="t-xs">
          ACI
        </div>
      </div>
    </div>
    <div flex class="mb-20 user-info">
      <div flex-box="0" style='margin-right:1rem;'>
        <i style='color:#7ED321;font-size:2rem;' class="iconfont icon-caifang-tianbaoren"></i>
      </div>
      <div flex-box="1">
        <div class="mb-10"></div>
        <div class="t-xs mb-10">
          ACI
        </div>
        <div class="t-xs">
          <mt-button type='danger' size='small'></mt-button>
        </div>
      </div>
    </div>
    <div flex class="mb-20 user-info">
      <div flex-box="0" style='margin-right:1rem;'>
        <i style='color:#FF5900;font-size:2rem;' class="iconfont icon-course"></i>
      </div>
      <div flex-box="1">
        <div class="mb-10"></div>
        <div class="t-xs mb-10">
          ACI
        </div>
        <div class="t-xs">
          <mt-button type='danger' size='small'></mt-button>
        </div>
      </div>
    </div>
    <div class="mb-20">
      <mt-cell title="" is-link>
        <span style="color: green"></span>
      </mt-cell>
      <mt-cell @click.native='$router.push("/courseList")' title="" is-link>
        <span>1</span>
      </mt-cell>
      <mt-cell title="" is-link>
        <span>010-51657777</span>
      </mt-cell>
    </div>
    <div class="t-center user-logout">
      <mt-button @click.native='doLogout' style="width:60vw;"></mt-button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    doLogout() {
      this.$storage.remove("userToken");
      this.$router.push("/login");
    }
  }
};
</script>
<style lang="less" scoped>
.page {
  padding: 40px 0 50px;
  //height: 100vh;
  //box-sizing: border-box;
}
.user-info {
  padding: 1rem;
  background-color: white;
  .user-avatar {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 2.5rem;
  }
}
.user-logout {
  //position: fixed;
  margin-top: 3rem;
  width: 100vw;
  bottom: 3rem;
}
</style>



